      <li><a href="https://github.com/vuejs/awesome-vue" target="_blank">awesome-vue</a></li>
    </ul>
    <div class="" id="allmap" style="height:400px; width: 100%"></div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
// import minutils from '@/utils/minUtils'
export default {
  mounted () {
    // utils.addClass(this.$refs.testClass, 'testClass')
    // this.showToast()
    // this.showMap()
    this.showLocation()
  },
  methods: {
    showToast () {
      // minutils.Toast({ message: '操作成功', iconClass: 'icon icon-success' })
      // minutils.MessageBox('提示001', 'teetetetet')
    },
    showMap () {
      var map = new BMap.Map('allmap')
      map.centerAndZoom(new BMap.Point(116.404, 39.915), 11)
      map.addControl(new BMap.MapTypeControl())
      map.setCurrentCity('北京')
      map.enableScrollWheelZoom(true)
    },
    showLocation () {
      utils.getCurrentLocation()
    }
  }
}
</script>

